#invoicePopUp {
  background-color: var(--background);
  width: 600px;
  border-radius: 10px;
  /* overflow: hidden; */
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

#invoicePopUp > .header {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

select,
#orders,
#suppliers {
  width: 90%;
  font-size: 30px;
  font-weight: 500;
  background-color: transparent;
  outline: none;
  border: none;
  color: #959595;
  border-bottom: 1px solid var(--primary-text);
  padding: 10px;
}

option {
  font-size: 20px;
  font-weight: 500;
  color: #959595;
  outline: none;
  text-decoration: none;
  background-color: var(--background);
}

#date {
  margin-left: 30px;
  align-self: flex-start;
  border-radius: 10px;
  padding: 5px;
  font-size: 25px;
  color: #959595;
  border: 1px solid var(--primary-text);
  width: 40%;
}
