#categoryPopUp {
  background-color: var(--background);
  width: 600px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.error {
  color: red;
  font-size: 24px;
}
