.greeting {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.greeting > h1 {
  font-size: 38px;
  font-weight: 400;
  cursor: default;
}

.carousel-slide {
  height: 220px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

#main-workspace {
  width: 800px;
  margin: 20px auto 0;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

#summaryTileContainer {
  display: flex;
  justify-content: space-around;
}

#recentActivity {
  width: 100%;
  position: relative;
}

#activityListContainer {
  height: 210px;
  overflow: scroll;
}

#recentOrders {
  width: 100%;
  position: relative;
}

#activityList {
  padding: 10px;
  width: 100%;
  /* height: 200px; */
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.section-title {
  font-size: 22px;
  font-weight: 500;
  color: var(--primary-text);
  margin-bottom: 10px;
  cursor: default;
}

.carousel-slider {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel-btn {
  width: 40px;
  cursor: pointer;
  color: var(--secondary-color);
  transform: translateY(-50%);
}

.indicator {
  width: 12px;
  height: 12px;
  border: var(--secondary-color) 1px solid;
  overflow: hidden;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 2px 2px;
  transition: all 0.3s ease-in-out;
}

.indicator-selected {
  background-color: var(--secondary-color);
}

.indicator:hover {
  background-color: var(--secondary-color);
}

.control-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
